import { useRecoilValue } from "recoil";
import { FENState, rotateState } from "../utils/recoil";
import ChessBoard from "./ChessBoard";
import GameInform from "./GameInform";
import PlayerCard from "./PlayerCard";
import { BottomGameInform, ChessBoardWrap, GameInformWrap, PageContent, PageWrap, PlayerAndBoard, PlayerCardWrap } from "./style";

const Game = () => {
  const FEN = useRecoilValue(FENState);
  const rotate = useRecoilValue(rotateState);
  return (
    <PageWrap>
      <PageContent>
        <PlayerAndBoard
          rotate={rotate}
        >
          <PlayerCardWrap>
            <PlayerCard
              ID=""
              rating={2200}
              title="GM"
              color="b"
              capturedPieces={["Q", "R", "R", "B", "B", "N", "N", "P", "P", "P", "P", "P", "P", "P", "P"]}
              pieceScore={1}
              state="normal"
            />
          </PlayerCardWrap>
          <ChessBoardWrap>
            <ChessBoard />
          </ChessBoardWrap>
          <PlayerCardWrap>
            <PlayerCard
              ID=""
              rating={1800}
              title="IM"
              color="w"
              capturedPieces={["q", "r", "r", "b", "b", "n", "n", "p", "p", "p", "p", "p", "p", "p", "p"]}
              pieceScore={-1}
              state="normal"
            />
          </PlayerCardWrap>
        </PlayerAndBoard>
        <GameInformWrap>
          <GameInform/>
        </GameInformWrap>
      </PageContent>
      <BottomGameInform>
        FEN: {FEN} <br />
      </BottomGameInform>
    </PageWrap>
  )
}

export default Game;